import { RPTooltip } from "@rp/react/web/common/base-components"
import { styled, tooltipClasses } from "@rp/react/web/mui/core"
import { ExtraProps } from "./EllipsisTooltip.types"

export const StyledTooltip = styled(
  ({ className, ...props }: ExtraProps) => <RPTooltip {...props} classes={{ popper: className }} />,
  { name: "EllipsisTooltip", shouldForwardProp: prop => prop !== "tooltipStyles" },
)(props => ({
  [`& .${tooltipClasses.tooltip}`]: {
    ...props.tooltipStyles,
  },
}))
